.appdownload {
    margin: auto;
    margin-top: 100px;
    font-size: max(3vw, 20px);
    text-align: center;
    font-weight: 500;
    color: #03c04a;
    .appdownload_platforms {
        display: flex;
        justify-content: center;
        gap: max(2vw, 10px);
        margin-top: 40px;
        img {
            width: max(30vw, 120px);
            max-width: 180px;
            transition: 0.5s;
            cursor: pointer;
            &:hover {
                transform: scale(1.1);
            }
        }
    }
}
