.food_display {
    margin-top: 30px;
    h2 {
        font-size: max(2vw, 24px);
        font-weight: 600;
        color: #3cec97;
    }
    .food_display_list {
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
        margin-top: 30px;
        gap: 30px;
        row-gap: 50px;
    }
}
